<template>
    <el-dialog
        title=""
        :visible.sync="openSignDialog"
        :show-close="false"
        :close-on-press-escape="false"
        :close-on-click-modal="false">
        <el-form :model="signForm" :rules="signRules" ref="signForm" :label-width="'120px'">
            <el-form-item label="" prop="unit">
                <el-select v-model="signForm.unit" placeholder="">
                    <el-option label="" :value="0"></el-option>
                    <el-option label="" value="month"></el-option>
                    <el-option label="" value="day"></el-option>
                    <el-option label="" value="hour"></el-option>
                    <el-option label="" value="time"></el-option>
                </el-select>
            </el-form-item>

            <el-form-item label="" prop="service_count">
                <el-input-number type="number" :max="999999" v-model.number="signForm.service_count"></el-input-number>
            </el-form-item>

            <el-form-item label="" >
                    <el-date-picker
                    v-model="service_period"
                    type="datetimerange"
                    @change="changeServicePeriod"
                    range-separator=""
                    start-placeholder=""
                    end-placeholder="">
                </el-date-picker>
            </el-form-item>

            <el-form-item label="" prop="unit_price">
                <el-input v-model="signForm.unit_price" :maxlength="11" autocomplete="off"></el-input>
            </el-form-item>

            <el-form-item label="" >
                <el-input v-model="signForm.total_price" :maxlength="11" autocomplete="off" disabled=""></el-input>
            </el-form-item>

            <el-form-item label="" prop="paper" ref="paper">
                <el-upload
                    accept=".jpg,.jpeg,.png,.gif,.bmp,.pdf,.JPG,.JPEG,.PBG,.GIF,.BMP,.PDF"
                    action="/admin/common/uploadImage"
                    :on-success="uploadSuccess"
                    :on-remove="removePic"
                    :file-list="signForm.paper"
                    list-type="picture-card"
                    :headers="uploadHeader">
                    <i class="el-icon-plus"></i>
                </el-upload>
            </el-form-item>

            <el-form-item label="" >
                <el-select v-model="signForm.pay_wage" placeholder="">
                    <el-option label="" :value="2"></el-option>
                    <el-option label="" :value="1"></el-option>
                </el-select>
            </el-form-item>

            <el-form-item label="" v-if="signForm.pay_wage == 2" prop="wage_count">
                <el-input-number :max="999999" v-model="signForm.wage_count" autocomplete="off"></el-input-number>
            </el-form-item>

            <el-form-item label="" v-if="signForm.pay_wage == 2" prop="wage_price">
                <el-input v-model="signForm.wage_price" :maxlength="11" autocomplete="off"></el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="closeDialog"> </el-button>
            <el-button type="primary" @click="signOrder('signForm')"> </el-button>
        </div>
    </el-dialog>
</template>
<script>
import {orderService} from '../../../../../../common'

// import paperComponent from '../../servicehr/staff/components/paperComponent.vue'
export default {
    components: {
        // paperComponent,
    },
    props: {
        //
        openSignDialog: {
            type: Boolean,
            default: false,
        },
        //id
        staffId: {
            type: Number,
            default: 0,
        },
        //
        staffName: {
            type: String,
            default: ""
        },
        //id
        order_staff_id: {
            type: Number,
            default: 0,
        }
    },
    data(){
        var _this = this
        const validator = {
            //
            unit(rule, value, callback){
                if (value == '0') {
                    callback(new Error(''));
                } else {
                    callback()
                }
            },
            //
            service_count(rule, value, callback){
                if (value == 0) {
                    callback(new Error(''));
                } else {
                    callback()
                }
            },
            //
            unit_price(rule, value, callback){
                if (!value) {
                    callback(new Error(''));
                } else if(!/^\d+(\.\d+)?$/.test(value) && value != 0){
                    callback(new Error(''));
                } else {
                    callback()
                }
            },
            //
            wage_count(rule, value, callback){
                if(_this.signForm.pay_wage == 2){
                    if (value == 0) {
                        callback(new Error(''));
                    } else {
                        callback()
                    }
                } else {
                    callback()
                }
            },
            //
            wage_price(rule, value, callback){
                if(_this.signForm.pay_wage == 2){
                    if (value == 0) {
                        callback(new Error(''));
                    } else {
                        callback()
                    }
                } else {
                    callback()
                }
            },
            //
            paper(rule, value, callback){
                if (value.length == 0) {
                    callback(new Error(''));
                } else {
                    callback()
                }
            }
        }
        return {
            signRules: {
                //
                unit: [
                    { validator: validator.unit, trigger: 'change' }
                ],
                //
                service_count: [
                    { validator: validator.service_count, trigger: ['blur', 'change'] }
                ],
                //
                unit_price: [
                    { validator: validator.unit_price, trigger: 'blur' }
                ],
                //
                wage_count: [
                    { validator: validator.wage_count, trigger: 'blur' }
                ],
                //
                wage_price: [
                    { validator: validator.wage_price, trigger: 'blur' }
                ],
                //
                paper: [
                    { validator: validator.paper, trigger: 'change' }
                ],
            },
            //
            signForm: {
                order_id: this.$route.query.order_id,// id
                order_staff_id: this.order_staff_id,//id
                staff_id: this.staffId,// id
                staff_name: this.staffName,//
                unit: 0,// 
                service_count: 0,// 
                paper: [],//
                service_start_time: 0,//
                service_end_time: 0,//

                unit_price: 0,// 
                total_price: 0,// 
                pay_wage: 1,// 
                wage_count: 0,// 
                wage_price: 0,// 
                version: store.state.orderModule.order.version,
            },
            service_period:[],//
            //header
            uploadHeader:{
                accessToken: this.$store.state.loginModule.token.access_token
            }
        }
    },
    computed:{
        total_price(){
            return parseFloat((Number(this.signForm.service_count) * Number(this.signForm.unit_price)).toFixed(10))
        }
    },
    watch: {
        total_price:function(val){
            this.signForm.total_price = val
        }
    },
    methods: {
        /**
         * 
         */
        changeServicePeriod(param){
            this.signForm.service_start_time = param[0].getTime()
            this.signForm.service_end_time = param[1].getTime()
        },
        /**
         * 
         */
        uploadSuccess(response, file, fileList) {
            let picItem = {
                path: response.data.path,
                url: './resource/'+response.data.path,
                name: response.data.name
            }
            this.signForm.paper.push(picItem)
            //
            if(this.signForm.paper.length){
                this.$refs.paper.clearValidate()
            }
        },
        /**
         * 
         */
        removePic(file, fileList){
            this.signForm.paper.forEach((item, index) =>{
                if(item.name == file.name){
                    this.signForm.paper.splice(index,1)
                }
            })
        },
        /**
         * 
         */
        async signOrder(formName){
            this.$refs[formName].validate(async (valid) => {
                if (valid) {

                    store.commit('setLoading',true)

                    await orderService.sign(this.signForm).then(data =>{
                            if(data.code == "0"){
                                this.$message({
                                    type:'success',
                                    message: ``
                                })
                            //
                            this.$emit('closeSignDialog')
                            }
                        }).catch(e =>{
                            this.$message({
                                type:'error',
                                message: e.message
                            })
                        }).finally(async () =>{
                            await orderService.getOrder(this.$route.query.order_id)

                            store.commit('setLoading',false)
                        })
                } else {
                    return false;
                }
            });
        },
        /**
         * 
         */
        closeDialog(){
            //
            this.$emit('closeSignDialog')
        }
    }
}
</script>
<style lang="scss" scoped>

</style>





