<template>

    <el-dialog
    v-loading="loading"
        title=""
        :visible.sync="openAssignDialog"
        :show-close="false"
        :close-on-press-escape="false"
        :close-on-click-modal="false">
        <div class="base-form-box" :style="{height: `${(sizeLong / 2)*40}px`}">
            <div
                class="base-line"
                :style="{
                    width: item.size == 2? '100%' : '50%',
                    marginBottom: index == baseList.length - 1? '20px': '0'}"
                v-for="(item, index) in baseList"
                :key="index">
                <div class="base-word" v-if="item.key">
                    <div class="base-key">{{item.key+''}}</div>
                    <div class="base-value" v-if="item.value">{{item.value}}</div>
                    <div class="base-value" style="color: #F56C6C" v-else></div>
                </div>
                <div class="base-word" v-else>
                    <div class="base-key"></div>
                    <div class="base-value"></div>
                </div>
            </div>
        </div>
        <el-form ref="assignOrderForm" class="assign-form" :model="assignOrderForm" :rules="assignOrderRules" label-width="120px">
            <el-form-item label="" prop="manager_id">
                <el-select v-model="assignOrderForm.manager_id" placeholder="">
                    <el-option
                        v-for="(item, index) in selectionList"
                        :key="index"
                        :label="item.manager_name"
                        :value="item.manager_id"></el-option>
                </el-select>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="closeDialog"> </el-button>
            <el-button type="primary" @click="assignOrder('assignOrderForm')"> </el-button>
        </div>
    </el-dialog>

</template>
<script>

import {orderService, $utils} from '../../../../../common'
export default {
    props: {
        //
        openAssignDialog: {
            type: Boolean,
            default: false,
        },
        //id
        assignOrderId: {
            type: Number,
            default: 0
        }
    },
    data(){
        const managerNameValidator = function(rule, value, callback){
            if (value == 0) {
                callback(new Error(''));
            } else {
                callback()
            }
        }
        return {
            orderKeyName: {
                manager_name: "", //
                user_name: '',//
                phone: '',//
                name: '',//
                service_address: '',//
                service_start_time: '',//
                service_end_time: '',//
                source: '',//
                remark: '',//
                create_manager_name: '',
                hold_manager_name: "",
                // maintain_manager_name: "",
                sign_manager_name: "",
            },
            assignOrderForm: {
                order_id: this.assignOrderId,
                manager_name: '',
                manager_id: 0,
            },
            selectionList: [],
            assignOrderRules: {
                manager_id: [
                    { validator: managerNameValidator, trigger: 'change'}
                ]
            },
            sizeLong: 0, //
            baseList:[],
            loading: false
        }
    },
    computed: {
        /**
         *  from vuex
         */
        order_source(){
            return this.$store.state.orderModule.order_source
        },
        /**
         * 
         */
        orderDetail(){
            return this.$store.state.orderModule.order
        },
    },
    methods: {
        /**
         * 
         */
        closeDialog(){
            //
            this.$emit('closeAssignDialog')
        },
        /**
         * 
         */
        async assignOrder(formName){
            let _this = this
            this.$refs[formName].validate(async (valid) => {
                if (valid) {
                    let selectedObject = _this.selectionList.find((item, index) =>{
                        return item.manager_id == _this.assignOrderForm.manager_id
                    })
                    this.assignOrderForm.manager_name = selectedObject.manager_name
                    this.assignOrderForm.version = this.$store.state.orderModule.order.version
                    try{
                        this.loading = true
                        await orderService.assignOrder(this.assignOrderForm).then((data) =>{
                            if(data.code == "0"){
                                this.$message({
                                    type:'success',
                                    message: data.message
                                })
                                //
                                this.$emit('closeAssignDialog')
                            }
                        }).catch(error =>{
                            this.$message({
                                type:'error',
                                message: error.message
                            })
                        }).finally(() =>{
                            this.loading = false
                        })
                    } catch(error){
                        this.$message({
                            type:'error',
                            message: error.message
                        })
                    }
                } else {
                    return false;
                }
            });
        },
        setList(){
            let _this = this,
                formArray = [],//
                sizeLong1 = 0;//

            /**
             * key 
             * value 
             * size 
             */
            Object.keys(_this.orderDetail).forEach((item, index) =>{
                if(_this.orderKeyName.hasOwnProperty(item)){

                    let itemObj = {
                            key: _this.orderKeyName[item], //
                            value: _this.orderDetail[item],//
                            size: 1,//  1  2 
                        }, //
                        realValue = itemObj.value; //value

                    //
                    if(item == "source"){

                        realValue = this.order_source.find((item, index) =>{
                            return realValue == item.value
                        }).label
                    }

                    //
                    if(item == "service_start_time" || item == "service_end_time"){

                        realValue = $utils.formatDate(new Date(realValue * 1000), 'yyyy-MM-dd hh:mm:ss')

                    }

                    //
                    if($utils.getByteLen(realValue) > 20){
                        itemObj.size = 2 //
                    } else {
                        itemObj.size = 1//
                    }

                    itemObj.value =  realValue //value

                    formArray.push(itemObj)
                }
            })

            this.sizeLong = 0;


            let sizeNumber = 0;
            for(let i = 0; i<formArray.length; i++){
                sizeNumber += formArray[i].size
                if(i < formArray.length -1){
                    let j = i+1,
                        nextObject = formArray[j]
                    if(sizeNumber%2 == 1 && nextObject.size == 2){
                        this.sizeLong += 2
                        formArray[i].size = 2
                        sizeNumber += 1
                    }
                    this.sizeLong += formArray[i].size
                } else {
                    this.sizeLong += formArray[i].size
                }
            }



            //size
            if(this.sizeLong%2 == 1){
                formArray.push({
                    key:  "",
                    value: "",
                    size:1,
                })
                this.sizeLong++;
            }

            return formArray
        }
    },
    async mounted(){
        try{
            this.loading = true

            await Promise.all([
                orderService.getManagerSelection(),
                orderService.getOrder(this.assignOrderId),
            ]).then((data) =>{
                if(data[0].code == "0"){
                    this.selectionList = data[0].data
                    this.selectionList.push({
                        manager_name: '',
                        manager_id: 0,
                    })
                }
                this.baseList = this.setList()
            }).catch(error =>{
                this.$message({
                    type:'error',
                    message: error.message
                })
            }).finally(() =>{
                this.loading = false
            })

        } catch(error){
            this.$message({
                type:'error',
                message: error.message
            })
        }

    }
}
</script>
<style lang="scss" scoped>

        .base-form-box{
            .base-line{
                float: left;
                display: flex;
                line-height: 40px;
                height:40px;
                .base-word{
                    display: flex;
                    .base-key{
                        width: 120px;
                        padding-right: 10px;
                        text-align: right;
                    }
                    .base-value{
                        flex: 1;
                    }
                }
            }
        }
        .assign-form{
            width:100%;
        }

</style>





