<template>
    <!--  -->
    <el-dialog
        title=""
        :visible.sync="paperDialogVisible"
        :show-close="false"
        :close-on-press-escape="false"
        :close-on-click-modal="false">
        <el-form :model="paperForm" label-width="120px" :rules="paperRules" ref="paperForm">

            <el-form-item label="" prop="paper_category_id" style="margin-bottom:30px;">
                <el-select v-model="paperForm.paper_category_id" placeholder="" :disabled="isEditPaper">
                    <el-option v-for="item in paperCategoryList" :key="item.id" :label="item.name" :value="item.id"></el-option>
                </el-select>
            </el-form-item>

            <el-form-item label="" prop="images" ref="paper">
                <el-upload
                    accept=".jpg,.jpeg,.png,.gif,.bmp,.pdf,.JPG,.JPEG,.PBG,.GIF,.BMP,.PDF"
                    action="/admin/common/uploadImage"
                    :on-success="uploadSuccess"
                    :on-remove="removePic"
                    :file-list="paperForm.images"
                    list-type="picture-card"
                    :headers="uploadHeader">
                    <i class="el-icon-plus"></i>
                </el-upload>
            </el-form-item>

        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="cancelPaper"> </el-button>
            <el-button type="primary" @click="surePaper('paperForm')"></el-button>
        </div>
    </el-dialog>
</template>

<script>

/**
 * type 0   1 
 */
import {hrService} from '../../../../../../common'

export default {
    props:{
        /**
         * 
         */
        paperDialogVisible:{
            default:false,
            type: Boolean,
        },
        /**
         * 
         * true  false 
         */
        isEditPaper: {
            default: false,
            type: Boolean,
        },
        /**
         * 
         * 
         */
        paperProps: {
            type: Object,
            default: function(){ return {} }
        },
        /**
         * 
         */
        selectedPapers: {
            type: Array,
            default: function(){ return [] }
        },
    },
    data() {
        var _this = this;
        const validator = {
            validatePaper_category_id(rule, value, callback){
                //
                let isHave = _this.selectedPapers.some((item, index) =>{
                    return item.paper_category_id == value
                })
                if (!value) {
                    callback(new Error(''));
                } else if(isHave && !_this.isEditPaper){
                    callback(new Error(''));
                } else {
                    callback();
                }
            },
            validateImages(rule, value, callback){
                if (value.length == 0) {
                    callback(new Error(''));
                } else {
                    callback();
                }
            }
        }
        return {
            //
            paperRules: {
                paper_category_id: [
                    {validator: validator.validatePaper_category_id, trigger:'change'}
                ],
                images: [
                    {validator: validator.validateImages, trigger:'change'}
                ],
            },
            //props
            paperForm: {
                id: null,
                paper_category_name: this.paperProps.paper_category_name,//
                paper_category_id: this.paperProps.paper_category_id,//id
                images: this.paperProps.images,//
            },
            //
            paperCategoryList: [],
            //header
            uploadHeader:{
                accessToken: this.$store.state.loginModule.token.access_token
            }
        }
    },
    methods: {
        /**
         * 
         */
        uploadSuccess(response, file, fileList) {
            let picItem = {
                path: response.data.path,
                url: './resource/'+response.data.path,
                name: response.data.name
            }
            this.paperForm.images.push(picItem)
            
            //
            if(this.paperForm.images.length){
                this.$refs.paper.clearValidate()
            }
        },
        /**
         * 
         */
        removePic(file, fileList){
            this.paperForm.images.forEach((item, index) =>{
                if(item.name == file.name){
                    this.paperForm.images.splice(index,1)
                }
            })
        },
        /**
         * 
         */
        surePaper(formName){

            let _this = this;

            this.$refs[formName].validate((valid) => {
                if (valid) {
                    /**
                     * 
                     * des: Uidthis.paperCategoryList
                     * idthis.paperFormidpaper_category_id
                     * 
                     * findthis.paperForm
                     */
                    this.paperForm.paper_category_name = this.paperCategoryList.find((item, index) =>{
                        return item.id == _this.paperForm.paper_category_id
                    }).name
                    
                    //
                    this.$emit('changePaper', this.paperForm, this.isEditPaper)

                } else {
                    return false;
                }
            });
        },
        /**
         * ---
         */
        cancelPaper(){
            this.$emit('closePaper')
        }
    },
    async mounted(){
        store.commit('setLoading',true)
        try{
            let data = await Promise.all([
                hrService.getPaperSelection('enable'), //
            ])

            //promise.all 
            this.paperCategoryList = data[0].data

        }catch(error){
            this.$message({
                type:'error',
                message: error.message
            })
        }
        store.commit('setLoading',false)
    }
}
</script>





