<template>
    <div class="order-edit">
        <el-form ref="orderForm" :model="orderForm" :rules="orderRules" label-width="120px" class="order-edit-form">

            <el-form-item label="" prop="skill">
                <el-tag
                @close="handleClose"
                v-if="orderForm.service_category_id"
                closable>{{orderForm.name}}</el-tag>

                <el-cascader
                    v-if="orderForm.service_category_id == ''"
                    clearable
                    :options="skillList"
                    v-model="skill"
                    :props="areaProps"
                    placeholder="">
                </el-cascader>
                <el-button
                    icon="el-icon-plus"
                    circle
                    v-if="orderForm.service_category_id == ''"
                    @click="addSkill(skill, 'skill')">
                </el-button>
            </el-form-item>

            <el-form-item label="" prop="user_name">
                <el-input v-model="orderForm.user_name" :maxlength="20"></el-input>
            </el-form-item>

            <el-form-item label="" prop="phone">
                <el-input v-model="orderForm.phone" :maxlength="11"></el-input>
            </el-form-item>

            <el-form-item label="" class="area-form">
                <el-cascader
                    :options="areaList"
                    v-model="region_id"
                    :props="areaProps"
                    @change="getAreaName"
                    clearable
                    placeholder="">
                </el-cascader>
                <el-input placeholder="" :maxlength="180" v-model="region_string"></el-input>
            </el-form-item>
            <p class="address-details">
                <span
                    v-for="(item,index) in regionArea_string"
                    :key="index">{{item}}</span>
                <span>{{region_string}}</span>
            </p>

            <el-form-item label="">
                <el-date-picker
                    v-model="cycleTime"
                    type="datetimerange"
                    @change="changeCycleTime"
                    range-separator=""
                    start-placeholder=""
                    end-placeholder=""></el-date-picker>
            </el-form-item>

            <el-form-item label="" prop="source">
                <el-select v-model="orderForm.source" placeholder="">
                    <el-option label="" value="0"></el-option>
                    <el-option label="" value="2"></el-option>
                    <el-option label="" value="1"></el-option>
                    <el-option label="" value="3"></el-option>
                </el-select>
            </el-form-item>

            <el-form-item label="">
                <el-input placeholder="200" autosize :maxlength="100" type="textarea" v-model="orderForm.remark"></el-input>
            </el-form-item>

            <el-form-item>
                <el-button type="primary" @click="onSubmit('orderForm')"></el-button>
                <el-button @click="goback"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
import {hrService} from '../../../common'
import {orderService} from '../../../common'
export default {
    data(){
        const validator = {
            user_name(rule, value, callback){
                if (!value) {
                    callback(new Error(''));
                } else if (!/^[a-zA-Z\u4e00-\u9fa5]+$/.test(value)){
                    callback(new Error(''));
                } else {
                    callback()
                }
            },
            phone(rule, value, callback){
                if (!value) {
                    callback(new Error(''));
                } else if (!(/^1[34578]\d{9}$/.test(value ))){
                    callback(new Error(''));
                } else {
                    callback()
                }
            },
            service_address(rule, value, callback){
                if (!value) {
                    callback(new Error(''));
                } else {
                    callback()
                }
            },
            source(rule, value, callback){
                if (value == '0') {
                    callback(new Error(''));
                } else {
                    callback()
                }
            },
        }
        return {
            orderRules: {
                user_name: [
                    { validator: validator.user_name, trigger: 'blur' }
                ],
                phone: [
                    { validator: validator.phone, trigger: 'blur' }
                ],
                source: [
                    { validator: validator.source, trigger: 'change' }
                ],
            },
            orderForm: {
                manager_id: this.$store.state.loginModule.user.id, //
                manager_name: this.$store.state.loginModule.user.username, //
                user_name: '',//
                phone: '',//
                service_category_id: "",//id
                name: '',//
                service_address: '',//
                service_start_time: '',//
                service_end_time: '',//
                source: '0',//
                remark: '',//
            },
            cycleTime: [],//
            region_id: [], //
            areaList: [],//
            skill: [],//
            skillList: [],//
            regionArea_string: [],// 
            region_string: '',//
            //
            areaProps: {
                label: 'name',
                value: 'id'
            },
        }
    },
    methods: {
        async onSubmit(formName) {
            let str = "",
                arr = this.regionArea_string;

            for(let i = 0; i<arr.length; i++){
                str += arr[i]
            }
            this.orderForm.service_address = str + this.region_string

            //
            if(this.orderForm.service_category_id == '' || this.orderForm.name ==''){
                this.$message({
                    type:'error',
                    message: ''
                })
                return false;
            }

            //
            if(this.orderForm.service_address == '' || this.region_string =='' || arr.length == 0){
                this.$message({
                    type:'error',
                    message: ''
                })
                return false;
            }

            //
            if(this.orderForm.service_start_time == '' || this.orderForm.service_start_time == ''){
                this.$message({
                    type:'error',
                    message: ''
                })
                return false ;
            }
            //
            await this.$refs[formName].validate((valid) => {
                if (valid) {
                    orderService.createOrder(this.orderForm)
                    .then(data =>{

                        if(data.code == '0'){
                            this.$message({
                                type:"success",
                                message: ""
                            })
                            this.$router.push('/sale/orderList')
                        }
                    }).catch(error =>{
                        this.$message({
                            type:'error',
                            message: error.message
                        })
                    })
                } else {
                    return false;
                }
            });
        },
        /**
         * 
         */
        changeCycleTime(param){
            this.orderForm.service_start_time = param[0].getTime()
            this.orderForm.service_end_time = param[1].getTime()
        },
        /**
         * 
         */
        getAreaName(){
            let num = 0,
                arr = [],
                regionArr = this.region_id;

            function findAreaName(areaList){
                areaList.forEach((item, index) =>{
                    if(item.id == regionArr[num]){
                        arr.push(item.name)
                    }
                })

                areaList.forEach((item, index) =>{
                    if(num < 2 && item.id == regionArr[num]){
                        num++
                        findAreaName(item.children)
                    }
                })
            }

            findAreaName(this.areaList)
            this.regionArea_string = arr;
        },
        /**
         * 
         * @param cascaderData Array 
         * @param type String 
         */
        addSkill(cascaderData, type){
            if(cascaderData.length == 0){
                this.$message({
                    type:'error',
                    message: ``
                })
                return
            }

            let _this = this,
                levelArr = this.skillList //

            //tag
            findAreaObj(levelArr, cascaderData)

            /**
             * Tag
             * des id [1000, 10001, 10002]
             *  idtag
             * @param areaList Array  
             * @param region Array 
             */
            function findAreaObj(areaList, region){

                areaList.forEach((item, index) =>{

                    if(item.children){

                        findAreaObj(item.children, region)

                    } else {
                        if(item.id == region[region.length-1]){
                            _this.orderForm.service_category_id = item.id
                            _this.orderForm.name = item.name
                            _this.skill = []
                        }
                    }
                })
            }
        },
        /**
         * tag
         */
        handleClose(){

            this.orderForm.service_category_id = ""
        },
        /**
         * 
         */
        goback(){
            this.$router.push('/sale/orderList')
        }
    },
    async mounted(){
        store.commit('setLoading',true)
        try{
            let data = await Promise.all([
                hrService.getAreaTree(), //
                hrService.getSkillTree('enable'), //
            ])
            this.areaList = data[0].data
            this.skillList = data[1].data
        }catch(e){
            this.$message({
                type:'error',
                message: e.message
            })
        }

        store.commit('setLoading',false)
    }
}
</script>
<style lang="scss" scoped>
    .order-edit{
        padding: 30px 0 0 30px;
        .order-edit-form{
            max-width: 750px;
            width: 80%;
            .area-form{
                & /deep/ .el-form-item__content{
                    display: flex;
                    & /deep/ .el-cascader{
                        margin-right: 20px;
                        width: 200px;
                    }
                }
            }
            .address-details{
                line-height: 40px;
                padding-left: 120px;
            }
        }
    }
</style>





