<template>
    <el-card class="box-card">
        <div slot="header" class="card-header">
            <h3></h3>
            <div class="control">
                <el-button 
                    type="text" 
                    :icon="isShow? 'el-icon-arrow-up' : 'el-icon-arrow-down'" 
                    circle size="small" 
                    @click="changeFormState">{{isShow?``:``}}</el-button>
            </div>
        </div>

        <div class="base-form-box" >
            <div class="base-message-box" v-if="isShow">
                <div v-if="baseList.length">
                    <div 
                        class="base-line"
                        :style="{
                            width: item.size == 1? '100%' : '50%',
                            marginBottom: index == baseList.length - 1? '20px': '0'}"
                        
                        v-for="(item, index) in baseList"
                        :key="index">
                        <div class="base-word" v-if="item.key">
                            <div class="base-key">{{item.key+''}}</div>
                            <div class="base-value" v-if="item.value">{{item.value}}</div>
                            <div class="base-value" style="color: #F56C6C" v-else></div>
                        </div>
                        <div class="base-word" v-else>
                            <div class="base-key"></div>
                            <div class="base-value"></div>
                        </div>
                    </div>
                </div>
                <div v-else> </div>
            </div>
        </div>
        
    </el-card>
</template>
<script>
import {$utils} from '../../../../../common'
export default {
    data(){
        return {
            orderKeyName: {
                manager_name: "", //
                user_name: '',//
                phone: '',//
                name: '',//
                service_address: '',//
                service_start_time: '',//
                service_end_time: '',//
                source: '',//
                remark: '',//
                create_manager_name: '',
                hold_manager_name: "",
                // maintain_manager_name: "",
                sign_manager_name: "",
                code: ""
            },
            //
            isShow:true
        }
    },
    computed: {
        // baseForm(){
        //     return this.$store.state.orderModule.order
        // },
        baseList(){
            let obj = this.orderKeyName,
                baseForm = this.$store.state.orderModule.order,
                newArr = [];
            
            /**
             * key 
             * value 
             */
            Object.keys(baseForm).forEach((item, index) =>{
                if(obj.hasOwnProperty(item)){
                    let itemObj = {
                        key: obj[item],
                        value: baseForm[item]
                    }

                    //value
                    let realValue = itemObj.value,
                        realItemObj = {
                            value: ''
                        }
                    
                    //
                    if(item == "source"){
                        if(realValue == 1){
                            realItemObj.value = ""
                        } else if(realValue == 2){
                            realItemObj.value = ""
                        } else if(realValue == 3){
                            realItemObj.value = ""
                        }

                        itemObj = {
                            ...itemObj,
                            ...realItemObj,
                        }
                    } 
                    //
                    if(item == "service_start_time" || item == "service_end_time"){
                        realItemObj.value = $utils.formatDate(new Date(realValue * 1000), 'yyyy-MM-dd hh:mm:ss')
                        itemObj = {
                            ...itemObj,
                            ...realItemObj,
                        }
                    }
                    newArr.push(itemObj)
                }

            })

            newArr = newArr.map((item, index) =>{

                if($utils.getByteLen(item.value) > 20){
                    return {
                        ...item,
                        size: 1, //
                    }
                } else {
                    return {
                        ...item,
                        size: 2,//
                    }
                }
            })
            return newArr
        }
    },
    methods: {
        //
        changeFormState(){
            this.isShow = !this.isShow
        }
    },
}
</script>
<style lang="scss" scoped>
    .box-card{
        margin: 10px 0 10px 10px;
        & /deep/ .el-card__header{
            padding: 0 30px;;
        }
        & /deep/ .el-card__body{
            padding: 20px;
        }
        .card-header{
            height:50px;
            width:100%;
            padding-right:30px;
            display: flex;
            justify-content: space-between;
            & h3{
                line-height: 50px;
            }
            .control{
                padding: 9px 0;
            }
        }
        .base-form-box{
            height: 100%;
            width: 100%;
            .base-message-box{
                width: 100%;
                .base-line{
                    float: left;
                    display: flex;
                    line-height: 40px;
                    .base-word{
                        display: flex;
                        .base-key{
                            width: 120px;
                            padding-right: 10px;
                            text-align: right;
                        }
                        .base-value{
                            flex: 1;
                        }
                    }
                }
            }
            
            .baseForm{
                width: 80%;
                min-width: 500px;
            }
        }
        
    }
</style>





