import Vue from 'vue'
import Vuex from 'vuex'


Vue.use(Vuex)

import orderRequest from '../service/request/orderRequest.js'
import { Message } from 'element-ui';
/**
 * 
 */
var order_type = [
    {
        label: '',
        value: 0
    },
    {
        label: '',
        value: 1
    },
    {
        label: '',
        value: 2
    },
    {
        label: '',
        value: 3
    },
    {
        label: '',
        value: 4
    },
    {
        label: '',
        value: 5
    },
]

/**
 * 
 */
var order_source = [
    {
        label: "",
        value: 0
    },
    {
        label: "",
        value: 1
    },
    {
        label: "",
        value: 2
    },
    {
        label: "",
        value: 3
    },
]


export const orderModule = {
    state: {
        order: [],//
        order_staff: [],//
        order_files: [], //
        order_logs: [],//

        order_type: order_type,//
        order_source: order_source,//
    },
    mutations: {
        /**
         * shuju
         */
        configData(state, data){
            state.order = data.data.order
            state.order_staff = data.data.order_staff //
            state.order_files = data.data.order_files //
            state.order_logs = data.data.order_logs //
        }
    },
    actions: {
        async setData(context, order_id){
            await orderRequest.getOrder(order_id)
                .then(data =>{
                    context.commit('configData',data)
                })
                .catch(e =>{
                    Message({
                        type:'error',
                        message: e.message
                    })
                })

        }
    }
}






