// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.

import '../common/style/index.css'

import '../common/style/styleConfig.scss'

import Vue from 'vue'

import axios from 'axios'

import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';

Vue.use(ElementUI);

Vue.prototype.$axios = axios;

//
import App from './App'

import '../common/config'

import {

    store, //vuex
    
    router,
    authService,//
    loginService,
} from '../common'

Vue.config.productionTip = false






/**
 * 
 */

 //ajax
// axios.defaults.baseURL = config.apiPath
// axios.defaults.timeout = 10000
axios.interceptors.request.use(config => {
    //token
    config.headers = {
        accessToken: store.state.loginModule.token != null? store.state.loginModule.token.access_token: ''
    }
    return config;
  
} , error => {
    throw error
});

// 
axios.interceptors.response.use(async response => {
    
    //
    if(response.data.code == "1"){
        throw response.data
    }
    //token 
    else if(response.data.code == "10001"){
        
        //token 
        await loginService.refreshToken(store.state.loginModule.token.refresh_token)

        //
        return axios(response.config)
    }
    //access_token 10002
    else if(response.data.code == "10002"){
        //
        await loginService.logout()
        //
        router.push('/login');
        throw response.data
    }
    //refresh_token 10003
    else if(response.data.code == "10003"){
        await loginService.logout()
        router.push('/login');
        throw response.data
    }
    else{
        return response.data;
    }

    
    
}, error => {
    // 
    throw error
});



new Vue({
    el: '#app',
    router,
    components: { App },
    template: '<App/>',
    store,
  })



