import shopRequest from './request/shopRequest.js'
import Axios from 'axios';

export default {
    /**
     * 
     * @param obj 
     */
    getShopList(obj){
        return shopRequest.getShopList(obj)
    },
    /**
     * 
     */
    getShop(id){
        return shopRequest.getShop(id)
    },
}


