<template>
    <el-dialog 
        :title="logDialogTitle" 
        :visible.sync="openLogDialog"
        :show-close="false"
        :close-on-press-escape="false"
        :close-on-click-modal="false">
        <el-form :model="logForm" :label-width="'120px'" ref="logForm" :rules="logRules">
            <el-form-item label="" v-if="logType == 'refuse'">
                <el-input v-model="logForm.staff_name" disabled></el-input>
            </el-form-item>
            <el-form-item label="" prop="message">
                <el-input placeholder="200" :maxlength="100" autosize v-model="logForm.message" type="textarea"></el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="closeLogDialog"> </el-button>
            <el-button type="primary" @click="commitLog('logForm')"></el-button>
        </div>
    </el-dialog>
</template>
<script>
import {orderService} from '../../../../../../common'

export default {
    props: {
        //
        openLogDialog: {
            type: Boolean,
            default: false,
        },
        //id
        staffId: {
            type: Number,
            default: 0,
        },
        //id
        order_staff_id: {
            type: Number,
            default: 0,
        },
        //
        staffName: {
            type: String,
            default: ""
        },
        //title
        logDialogTitle: {
            type: String,
            default: ""
        },
        /**
         * 
         * normal 
         * refuse 
         */
        logType: {
            type: String,
            default: 'normal'
        }
    },
    data(){
        const messageValidator = (rule, value, callback) => {
            if (value === '') {
                callback(new Error(''));
            } else {
                callback();
            }
        };
        return {
            //
            logForm : {
                order_id: this.$route.query.order_id,// id
                order_staff_id: this.order_staff_id,//id
                staff_name: this.staffName,//
                staff_id: this.staffId,// id
                message: '',//
            },
            //
            logRules: {
                message: [
                    { validator: messageValidator, trigger: 'blur' }
                ]
            }
        }
    },
    methods: {
        /**
         * 
         */
        async commitLog(formName){  
            this.$refs[formName].validate(async (valid) => {
                if (valid) {
                    
                    store.commit('setLoading',true)
            
                    await orderService.logCommit(this.logForm, this.logType) 
                        .then(data =>{
                            if(data.code == "0"){
                                this.$message({
                                    type:'success',
                                    message: data.message
                                })
                            }
                        })
                        .catch(e =>{
                            this.$message({
                                type:'error',
                                message: e.message
                            })
                        })
                    
                    await orderService.getOrder(this.$route.query.order_id)

                    store.commit('setLoading',false)

                    //
                    this.$emit('closeLogDialog')
                } else {
                    return false;
                }
            });
            
        },  
        //
        closeLogDialog(){
            //
            this.$emit('closeLogDialog')
        }
    }
}
</script>
<style lang="scss" scoped>


</style>





