<template>
    <div class="staff" v-loading="isLoaded">
        <div class="list-table">
            <div class="search-list">
                <query-component></query-component>
            </div>
            <div class="table-list">
                <div class="search-form">
                    <div class="search-left">
                        <el-input v-model="staffSearch.name" placeholder="" :maxlength="20"></el-input>
                        <el-input v-model="staffSearch.phone" placeholder="" :maxlength="20"></el-input>
                        <el-button type="primary" @click="searchStaff"></el-button>
                        <el-button type="primary" @click="resetStaff"></el-button>
                    </div>
                    <el-button type="primary" @click="createStaff"></el-button>
                </div>  
                <el-table :data="staffTable" class="staff-table" :stripe="true" border :fit="true"
                    height="calc(100vh-90px)"
                    :cell-style="{height: '30px',padding: '2px 0',}"
                    :header-cell-style="{height: '30px',fontSize: '12px',padding: '0px!important',}">

                    <el-table-column  label="" prop="staff_code" align="center" width="150"></el-table-column>

                    <el-table-column  label="" prop="sex" align="center" width="70">
                        <template slot-scope="scope">
                            <el-tag class="tag-style" size="medium">{{ scope.row.sex == 1?'':'' }}</el-tag>
                        </template>
                    </el-table-column>

                    <el-table-column  label="" prop="type" align="center" width="100">
                        <template slot-scope="scope">
                            <el-tag class="tag-style" size="medium">{{ scope.row.sex == 'sign'?'':'' }}</el-tag>
                        </template>
                    </el-table-column>

                    <el-table-column  label="" prop="created_at" align="center" :formatter="created_atFormatter" width="155"></el-table-column>

                    <el-table-column  label="" prop="register_at" :formatter="register_atFormatter" align="center" width="110"></el-table-column>
                    
                    <el-table-column  label="" prop="authentication" align="center" width="160">
                        <template slot-scope="scope">
                            <table-tag-component v-if="workerConfigList.authentication" :propList="workerConfigList.authentication" :tableOriginData="scope.row.authentication"></table-tag-component>
                        </template>
                    </el-table-column>
                    
                    <el-table-column  label="" prop="name" align="center" fixed="left"></el-table-column>
                    
                    <el-table-column  label="" prop="age" align="center" width="60"></el-table-column>
                    
                    <el-table-column  label="" prop="phone" align="center" width="120"></el-table-column>
                    
                    <el-table-column  label="" prop="return_msg" align="center" width="150">
                        <template slot-scope="scope">
                            <el-popover trigger="click" placement="top">
                                <p>{{ scope.row.return_msg }}</p>
                                <div slot="reference" >
                                    <p style=" overflow:hidden;text-overflow:ellipsis;white-space:nowrap;width: 129px;">{{ scope.row.return_msg }}</p>
                                </div>
                            </el-popover>
                        </template>
                    </el-table-column>
                    
                    <el-table-column  label="" prop="working_status" align="center" width="130">
                        <template slot-scope="scope">
                            <table-tag-component v-if="workerConfigList.working_status" :propList="workerConfigList.working_status" :tableOriginData="scope.row.working_status"></table-tag-component>
                        </template>
                    </el-table-column>
                    
                    <el-table-column  label="" prop="remarks" align="center" width="150">
                        <template slot-scope="scope">
                            <el-popover trigger="click" placement="top">
                                <p>{{ scope.row.remarks }}</p>
                                <div slot="reference" >
                                    <p style=" overflow:hidden;text-overflow:ellipsis;white-space:nowrap;width: 129px;">{{ scope.row.remarks }}</p>
                                </div>
                            </el-popover>
                        </template>
                    </el-table-column>
                    <el-table-column  label="" prop="skill" align="center">

                    </el-table-column>
                    <el-table-column  label="" prop="service_type" align="center" width="200">
                        <template slot-scope="scope">
                            <table-tag-component v-if="workerConfigList.service_type" :propList="workerConfigList.service_type" :tableOriginData="scope.row.service_type"></table-tag-component>
                        </template>
                    </el-table-column>
                    <el-table-column  label="" prop="service_crowd" align="center" width="120">
                        <template slot-scope="scope">
                            <table-tag-component v-if="workerConfigList.service_crowd" :propList="workerConfigList.service_crowd" :tableOriginData="scope.row.service_crowd"></table-tag-component>
                        </template>
                    </el-table-column>
                    <el-table-column  label="" prop="working_age" align="center" width="100">
                        <template slot-scope="scope">
                            <table-tag-component v-if="workerConfigList.working_age" :propList="workerConfigList.working_age" :tableOriginData="scope.row.working_age"></table-tag-component>
                        </template>
                    </el-table-column>
                    <el-table-column  label="" prop="working_experience" align="center" width="150">
                        <template slot-scope="scope">
                            <el-popover trigger="click" placement="top">
                                <p>{{ scope.row.working_experience }}</p>
                                <div slot="reference" >
                                    <p style=" overflow:hidden;text-overflow:ellipsis;white-space:nowrap;width: 129px;">{{ scope.row.working_experience }}</p>
                                </div>
                            </el-popover>
                        </template>
                    </el-table-column>
                    <el-table-column label="" prop="nation" align="center">
                        <template slot-scope="scope">
                            <table-tag-component v-if="workerConfigList.nation" :propList="workerConfigList.nation" :tableOriginData="scope.row.nation"></table-tag-component>
                        </template>
                    </el-table-column>
                    <el-table-column  label="" prop="birthPlace" align="center"></el-table-column>
                    <el-table-column  label="" prop="identify" align="center"></el-table-column>
                    <el-table-column  label="" prop="address" align="center" width="150">
                        <template slot-scope="scope">
                            <el-popover trigger="click" placement="top">
                                <p>{{ scope.row.address }}</p>
                                <p slot="reference" style="overflow:hidden;text-overflow:ellipsis;white-space:nowrap;width: 129px;">{{ scope.row.address }}</p>
                            </el-popover>
                        </template>
                    </el-table-column>
                    <el-table-column  label="" prop="region" align="center" width=""></el-table-column>
                    <el-table-column  label="" prop="education" align="center" :formatter="educationFomatter" width="120"></el-table-column>
                    <el-table-column  label="" prop="urgent_phone" align="center" width="150"></el-table-column>
                    <el-table-column  label="" prop="bank_card" align="center"></el-table-column>
                    <el-table-column  label="" prop="icon" align="center">
                        <template slot-scope="scope">
                            <img :src="scope.row.icon?'./resource/'+scope.row.icon:''" alt="" style="height: 24px;width: 24px;">
                        </template>
                    </el-table-column>
                    <el-table-column  label="" prop="course" align="center"></el-table-column>
                    <el-table-column  label="" prop="paper" align="center"></el-table-column>
                    <el-table-column  label="" prop="source" align="center">
                        <template slot-scope="scope">
                            <table-tag-component v-if="workerConfigList.source" :propList="workerConfigList.source" :tableOriginData="scope.row.source"></table-tag-component>
                        </template>
                    </el-table-column>
                    <el-table-column  label="" prop="manager_name" align="center" width="150"></el-table-column>

                    <el-table-column label="" align="center" fixed="right" width="150">
                        <template slot-scope="scope">
                            <el-button size="mini" type="text" @click="editStaff(scope.$index, scope.row)"></el-button>
                            <el-button size="mini" type="text" style="color:#f56c6c" v-if="scope.row.status == 0" @click="changeStaffStatus(scope.row)"></el-button>
                            <el-button size="mini" type="text" style="color:#67c23a" @click="changeStaffStatus(scope.row)" v-else></el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <!--  -->
                <el-pagination
                    class="pagination"
                    @current-change="handleCurrentPage"
                    @prev-click="handleCurrentPage"
                    @next-click="handleCurrentPage"
                    :current-page.sync="pagination.currentPage"
                    :page-size="pagination.pageNumber"
                    layout="prev, pager, next, jumper"
                    :total="pagination.total"></el-pagination>
            </div>
        </div>
    </div>
</template>
<script>
    import {hrService, $utils} from '../../../../common'
    import {
        cascaderComponent,
        tableTagComponent} from '@/pages/components'

    import {queryComponent} from './components'
    
    export default {
        components: {
            cascaderComponent,
            tableTagComponent,
            queryComponent
        },
        data() {
            return {
                //
                staffTable: [],
                //
                staffSearch: {
                    name: '', //
                    phone:'',//
                },
                isLoaded:false,
                /**
                 * 
                 */
                pagination: {
                    total: 0,
                    currentPage: 1,
                    pageNumber: 20,
                },
                // workerConfigList:{

                // },
                maxHeight:0
            }
        },
        computed:{
            /**
             * 
             */
            searchArray(){
                let arr = [],
                _this = this;

                Object.keys(this.staffSearch).forEach((item, index) =>{
                    //
                    if(Array.isArray(_this.staffSearch[item])){
                        if(_this.staffSearch[item].length){
                            let obj = {}
                            obj[item] = [..._this.staffSearch[item]]
                            obj = {
                                ...obj,
                                key: item
                            }
                            arr.push(obj)
                        }
                    }
                    //
                    else if(_this.staffSearch[item] != ''){
                        let obj = {}
                        obj[item] = _this.staffSearch[item]
                        obj = {
                            ...obj,
                            key: item
                        }
                        arr.push(obj)
                    }
                })
                return arr
            },
            workerConfigList(){
                return this.$store.state.hrModule.configForm
            }
        },
        methods: {
             /**
             * 
             * @param tableOption 
             * @param tableOption.currentPage 
             * @param tableOption.searchSelect Array 
             * [{searchkey: '', searchValue: ''}]
             */
            async getTableList(){

                let tableOption = {
                    currentPage: this.pagination.currentPage,
                    pageNumber: this.pagination.pageNumber,
                    get_for: 'staff',
                    searchSelect: this.searchArray
                }

                
                try{
                    
                    this.isLoaded = true

                    await Promise.all([
                        hrService.getFormConfig(), //
                        hrService.getStaffList(tableOption) //
                    ]).then((data) =>{
                        // this.workerConfigList = data[0].data
                        this.$store.commit('setConfigForm',data[0].data)



                        this.staffTable = data[1].data.data
                        //
                        this.pagination.currentPage = data[1].data.current_page //
                        this.pagination.total = data[1].data.total //
                    }).catch(error =>{
                        this.$message({
                            type:'error',
                            message: error.message
                        })
                    }).finally(() =>{
                        this.isLoaded = false
                    })

                } catch(error){
                    this.$message({
                        type:'error',
                        message: error.message
                    })
                }
            },
            /**
             * 
             */
            async handleCurrentPage(val){
                this.pagination.currentPage = val
                await this.getTableList()
            },
            /**
             * 
             */
            async searchStaff(){
                await this.getTableList()
            },
            /**
             * 
             */
            async resetStaff(){
                this.staffSearch.name= '' //
                await this.getTableList()
            },
            /**
             * 
             * des 
             */
            createStaff(){
                this.$router.push({
                    path: "/worker/workerItem",
                    query: {
                        type: 0
                    }
                })
            },
            /**
             * 
             * 
             */
            editStaff(index, row){
                this.$router.push({
                    path: "/worker/workerItem",
                    query: {
                        type: 1, //1
                        id: row.id
                    }
                })
            },
            /**
             * changeStaffStatus
             */
            async changeStaffStatus(row){
                let _this= this;

                let status = row.status == 0? '' : ''

                let response = await this.$confirm(`${status}?`, '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: `${status}`
                    });
                });

                if(response == "confirm"){
                    store.commit('setLoading',true)

                    try{
                        await hrService.changeStaffStatus(row.id, row.version)
                            .then(data =>{
                                if(data.code == "0"){
                                    this.$message({
                                        type:'success',
                                        message: `${status}`
                                    })
                                }
                            }).catch(e =>{
                                this.$message({
                                    type:'error',
                                    message: e.message
                                })
                            })
                    } catch(error){
                        this.$message({
                            type:'error',
                            message: error.message
                        })
                    }

                    await _this.getTableList()
                    store.commit('setLoading',false)
                }
            },
            rowStyleFunction(row, rowIndex){    
                console.log(row,rowIndex)
                if(rowIndex == 0){
                    return {height:'30px'}
                }
            },
            /**
             * 
             */
            created_atFormatter(row, column){
                return $utils.formatDate(new Date(row.created_at), 'yyyy-MM-dd hh:mm:ss')
            },
            //
            register_atFormatter(row, column){
                return $utils.formatDate(new Date(row.register_at), 'yyyy-MM-dd')
            },
            educationFomatter(row, column){
                // debugger
                let a =  this.$store.state.hrModule.educationList.filter(item => item.id == row.education)
                return a.length? a[0].name : ''
            }
        },
        async mounted(){
            // debugger
            // function getClientHeight()
            // {
            // var clientHeight=0;
            // if(document.body.clientHeight&&document.documentElement.clientHeight)
            // {
            // var clientHeight = (document.body.clientHeight<document.documentElement.clientHeight)?document.body.clientHeight:document.documentElement.clientHeight;
            // }
            // else
            // {
            // var clientHeight = (document.body.clientHeight>document.documentElement.clientHeight)?document.body.clientHeight:document.documentElement.clientHeight;
            // }
            // return clientHeight - 85;
            // }
            // this.maxHeight = getClientHeight()
            await this.getTableList()
        }
    }
</script>
<style lang="scss" scoped>
    .tag-style{
        height:24px;
        line-height: 24px;
    }
    .staff{


        .list-table{
            height: calc(100vh - 50px);
            width:100%;
            display: flex;
            .search-list{
                width: 180px;
                height: 100%;
                overflow-y: scroll;
                background: #eaedf1;
            }
            .table-list{
                flex:1;
                width: calc(100% - 180px);
                height: calc(100vh - 50px);
                .search-form{
                    height: 40px;
                    width: 100%;
                    display: flex;
                    justify-content: space-between;
                    .search-left{
                        display: flex;
                    }
                }
                // background: #185;
                .staff-table{
                    overflow: auto;
                    height: calc(100% - 72px);
                    width: 100%;
                    // min-width: 1100px;
                    margin: 0 auto;
                }
                .pagination{
                    height:32px;
                }
            }
        }
            
            
        


    }
</style>



