/**
 * 
 */
import axios from 'axios'

export default {
    /**
     * 
     * des token 
     * @param {username} 
     * @param {password} 
     */
    login(username, password){
        return axios.post(`./admin/login`,{
            account: username,
            password: password
        })
    },
    /**
     * token
     * @param refresh_token
     */
    refreshToken(refresh_token){
        return axios.post(`./admin/changeToken`,{
            refreshToken: refresh_token
        })
    }
}



