<template>
    <el-card class="box-card">
        <div slot="header" class="card-header">
            <h3></h3>
            <div class="control">
                <el-button 
                    type="text" 
                    :icon="isShow? 'el-icon-arrow-up' : 'el-icon-arrow-down'" 
                    circle size="small" 
                    @click="changeLogState">{{isShow?``:``}}</el-button>
            </div>
        </div>
        <div v-if="isShow">
            <div
                class="base-line"
                :style="{width: item.size == 2? '100%' : '50%'}"
                v-for="(item, index) in staffDetailList"
                :key="index">
                <div class="base-word" v-if="item.key">
                    <div class="base-key">{{item.key+''}}</div>
                    <div class="base-value" v-if="item.value" :style="{color: item.key==''? '#F56C6C' : ''}">{{item.value}}</div>
                    <div class="base-value" style="color: #F56C6C" v-else></div>
                </div>
                <div class="base-word" v-else>
                    <div class="base-key"></div>
                    <div class="base-value"></div>
                </div>
            </div>
            <div class="base-line base-array-line">
                <div class="base-word" >
                    <div class="base-key"></div>
                    <div class="base-value" v-if="staffDetailForm.region.length">
                        <el-tag v-for="(item, index) in staffDetailForm.region" :key="index">{{item.name}}</el-tag>
                    </div>
                    <div v-else style="color: #F56C6C"></div>
                </div>
            </div>
            <div class="base-line base-array-line">
                <div class="base-word" >
                    <div class="base-key"></div>
                    <div class="base-value" v-if="staffDetailForm.label.length">
                        <el-tag style="margin-right:10px;" v-for="(item, index) in staffDetailForm.label" :key="index">{{item.name}}</el-tag>
                    </div>
                    <div v-else style="color: #F56C6C"></div>
                </div>
            </div>
            <div class="base-line base-array-line">
                <div class="base-word" >
                    <div class="base-key"></div>
                    <div class="base-value" v-if="staffDetailForm.skill.length">
                        <el-tag v-for="(item, index) in staffDetailForm.skill" :key="index">{{item.name}}</el-tag>
                    </div>
                    <div v-else style="color: #F56C6C"></div>
                </div>
            </div>
        </div>

    </el-card>  
</template>
<script>
import {hrService, $utils} from '../../../../../common'
export default {
    props: {
        staffId: {
            type:Number,
            default: 0,
        }
    },
    data(){
        return{
            /**
             * 
             */
            staffKeyName: {
                address: "",
                age: "",
                name: "",
                identify: "",
                bank_card: "",
                phone: "",
                sex: "",
                wechat: "",
                education: "",
                type: "",
                staff_code: ""
            },
            /**
             * 
             * des 
             */
            staffDetailList: [],
            /**
             * 
             * des 
             */
            staffDetailForm: {
                region: [],
                label: [],
                skill: [],
            },
            isShow: false,//
            sizeLong: 0,
        }
    },
    methods: {
        setList(staffDetail){
            let _this = this,
                formArray = [],//
                sizeLong1 = 0;//
            
            /**
             * key 
             * value 
             * size 
             */
            Object.keys(staffDetail).forEach((item, index) =>{
                if(_this.staffKeyName.hasOwnProperty(item)){
                    
                    let itemObj = {
                            key: _this.staffKeyName[item], //
                            value: staffDetail[item],//
                            size: 1,//  1  2  
                        }, //
                        realValue = itemObj.value; //value
                    
                    //
                    if(item == "sex"){
                        realValue = staffDetail[item] == 1? "" : ""
                    }

                    //
                    if(item == "type"){
                        realValue = staffDetail[item] == 'sign'? "" : ""
                    }

                    //
                    if(item == "education"){
                        let educationList = _this.$store.state.hrModule.educationList
                        let education = educationList.find((it, index) =>{
                            return it.id == staffDetail[item]
                        })
                        realValue = education.name
                    } 
                    
                    //
                    if($utils.getByteLen(realValue) > 20){
                        itemObj.size = 2 //
                    } else {
                        itemObj.size = 1//
                    }

                    itemObj.value =  realValue //value

                    formArray.push(itemObj)
                }
            })

            this.sizeLong = 0;


            let sizeNumber = 0;
            for(let i = 0; i<formArray.length; i++){
                sizeNumber += formArray[i].size
                if(i < formArray.length -1){
                    let j = i+1,
                        nextObject = formArray[j]
                    if(sizeNumber%2 == 1 && nextObject.size == 2){
                        this.sizeLong += 2
                        formArray[i].size = 2
                        sizeNumber += 1
                    }
                    this.sizeLong += formArray[i].size
                } else {
                    this.sizeLong += formArray[i].size
                }
            }
            //size
            if(this.sizeLong%2 == 1){
                formArray.push({
                    key:  "",
                    value: "",
                    size:1,
                })
                this.sizeLong++;
            }
            
            return formArray
        },
        //
        changeLogState(){
            this.isShow = !this.isShow
        },
        /**
         * 
         */
        selectService(){
            this.$emit('sign',this.staffDetailForm)
        },
        cancelDetail(){
            this.$emit('closeDetailDialog')
        }
    },
    async mounted(){
        let _this = this
        
        try{
            await hrService.getStaff(this.staffId).then(data =>{
                if(data.code == "0"){

                    _this.staffDetailForm = data.data
                    _this.staffDetailList = _this.setList(data.data)
                }
            }).catch(e =>{
                this.$message({
                    type:'error',
                    message: e.message
                })
            }).finally(() =>{
            })
        } catch(error){
            this.$message({
                type:'error',
                message: e.message
            })
        }

    }
}
</script>
<style lang="scss" scoped>
    .line-bottom-1px{
        &:after{
            position: absolute;
            content: '';
            display: block;
            height: 1px;
            width: 100%;
            background: #ccc; 
            bottom: 0;
        }
    }
    .box-card{
        margin: 10px 0 10px 10px;
        & /deep/ .el-card__header{
            padding: 0 30px;
        }
        & /deep/ .el-card__body{
            padding: 20px;
        }
        .card-header{
            height:50px;
            width:100%;
            padding-right: 30px;
            display: flex;
            justify-content: space-between;
            & h3{
                line-height: 50px;
            }
            .control{
                padding: 9px 0;
            }
        }
        .service-box{

        }
    }

        .base-line{
            float: left;
            display: flex;
            line-height: 40px;
            height:40px;
            margin-bottom: 0px;
            .base-word{
                display: flex;
                .base-key{
                    width: 120px;
                    padding-right: 10px;
                    text-align: right;
                }
                .base-value{
                    flex: 1;
                }
            }
        }
        .base-array-line{
            width:100%;
        }
</style>





