/**
 * loading
 */


import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export const loadingModule = {
    state: {
        
        //handleErrortrueisShowErrorfalse
        isShowHandleError: false,
        isLoading: false,
    },
    mutations: {
        setLoading(state, loadingState){
            state.isLoading = loadingState
        }
    }
}


